/******************************************************
    MIXINS
    - general-typography rules
    - list-custom
 ******************************************************/

// General typography rules where needed
@mixin general-typography() {
    h2,
    h3,
    h4 {
        font-weight: $font-regular;
    }

    h2 {
        font-size: 3rem;
        margin-bottom: 2rem;
    }

    h3 {
        font-size: 2.5rem;
        margin-bottom: 1.5rem;
    }

    h4 {
        font-size: 1.8rem;
        margin-bottom: 1rem;
    }

    ul {
        @include list-custom;
    }
}

@mixin section-title($size: 'medium') {
    font-weight: $font-bold;
    font-size: 5rem;
    line-height: 1.2;

    & > * {
        font-size: inherit;
        font-weight: inherit;
        line-height: inherit;
    }

    &.big {
        font-size: 8rem;
        line-height: 1.1;
    }

    @if $size == 'small' {
        font-size: 3rem;
    }
}

@mixin image-fit($isAbsolute: false) {
    width: 100%;
    height: 100%;
    object-fit: cover;

    @if $isAbsolute {
        position: absolute;
        top: 0;
        left: 0;
    }
}

// List with custom bullet
@mixin list-custom() {
    list-style: none;
    padding-left: 0;
    li {
        display: flex;
        margin-bottom: 0.5rem;

        &:before {
            content: '';
            flex-shrink: 0;
            margin-top: 1.2rem;
            margin-right: 1.5rem;
            width: 0.5rem;
            height: 0.5rem;
            border-radius: 50%;
            background-color: $primary;
        }
    }
}
